import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	EventEmitter,
	Input,
	OnDestroy,
	OnInit,
	Output,
	SimpleChanges,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { debounceTime, distinctUntilChanged, map, skip, take } from 'rxjs/operators';

import { FabricDatePickerCalendarComponent } from './calendar/fabric-date-picker-calendar.component';
import { FabricDatePickerService } from './fabric-date-picker.service';
import { FabricDatePickerInlineDialogService } from './fabric.date-picker-inline-dialog.service';
import { FabricReactive } from '../../common/fabric-reactive';

import { Theme } from '../../themes/theme';
import { FabricDatePickerCompositionService } from './fabric-date-picker-composition.service';

@Component({
	selector: 'gui-date-picker',
	templateUrl: './fabric-date-picker.component.html',
	styleUrls: [
		'./fabric-date-picker.ngx.scss',
		'./themes/fabric-date-picker.dark.ngx.scss',
		'./themes/fabric-date-picker.material.ngx.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FabricDatePickerComponent extends FabricReactive implements OnInit, OnDestroy {

	@ViewChild('datePicker', { static: false })
	datePickerRef: ElementRef;

	@Input()
	parentElement: ElementRef;

	@Input()
	theme: Theme;

	@Input()
	selectDate: Date;

	@Input()
	name: string;

	@Input()
	openDialog: boolean = false;

	@Input()
	onlyDialog: boolean = false;

	@Input()
	datePipeOptions: string = 'dd/MM/yyyy';

	@Output()
	dateSelected = new EventEmitter();

	@Output()
	dialogOpened = new EventEmitter();

	datePickerForm: FormGroup;

	pickedDate: Date;

	inputDisabled: 'disabled' | '';

	constructor(private readonly fabricDatePickerInlineDialogService: FabricDatePickerInlineDialogService,
				private readonly datePickerService: FabricDatePickerService,
				private readonly datePickerCompositionService: FabricDatePickerCompositionService,
				private readonly formBuilder: FormBuilder,
				private readonly changeDetectorRef: ChangeDetectorRef) {
		super();
		this.datePickerForm = formBuilder.group({
				'date': ['']
			}
		);
	}

	ngOnChanges(changes: SimpleChanges) {

		if (changes.selectDate) {
			this.datePickerService.dateSelected(this.selectDate);
		}

		if (changes.onlyDialog) {
			this.inputDisabled = this.onlyDialog ? 'disabled' : ''; // todo !== null ??
		}

		if (changes.datePipeOptions) {
			this.datePickerCompositionService.next(this.datePipeOptions);
		}
	}

	ngOnInit() {

		this.datePickerService
			.observeSelectedDate()
			.pipe(
				take(1),
				this.takeUntil()
			)
			.subscribe(
				(date: Date) => {
					this.pickedDate = date;
					this.emitSelectedDate(date);
				}
			);

		this.datePickerService
			.observeSelectedDate()
			.pipe(
				skip(1),
				this.takeUntil()
			)
			.subscribe((date: Date) => {
					this.pickedDate = date;
					this.emitSelectedDate(date);
					this.changeDetectorRef.detectChanges();
					this.closeDatePicker();
				}
			);

		this.fabricDatePickerInlineDialogService
			.onOpened()
			.pipe(
				skip(1),
				this.takeUntil()
			)
			.subscribe((opened: boolean) => {
				this.dialogOpened.emit(opened);
			});

		this.observeDayChanges();
	}

	ngAfterViewInit() {
		if (this.openDialog) {
			this.openDatePicker();
		}
	}

	ngOnDestroy() {
		super.ngOnDestroy();
		this.fabricDatePickerInlineDialogService.close();
	}

	openDatePicker(): void {
		if (!this.parentElement) {
			this.parentElement = this.datePickerRef;
		}
		this.fabricDatePickerInlineDialogService
			.open(
				this.parentElement,
				FabricDatePickerCalendarComponent,
				this.theme
			);
	}

	closeDatePicker(): void {
		this.fabricDatePickerInlineDialogService.close();
	}

	private emitSelectedDate(date: Date): void {
		this.dateSelected.emit(date);
	}

	private observeDayChanges(): void {
		this.datePickerForm
			.controls['date']
			.valueChanges
			.pipe(
				distinctUntilChanged(),
				debounceTime(1500),
				map((day: string) => this.parse(day)),
				this.takeUntil()
			)
			.subscribe(
				(day: Date) => {
					this.datePickerService.dateSelected(day);
				});
	}

	private parse(value: string): Date { // TODO

		if ((typeof value === 'string') && (value.includes('/'))) {
			const str = value.split('/');

			const dateValues = this.getDateValues(str),
				dateHasAllValues = dateValues && dateValues.length === 3;

			if (dateHasAllValues) {
				return new Date(dateValues[0], dateValues[1], dateValues[2]);
			} else {
				return this.pickedDate;
			}
		} else {
			return this.pickedDate;
		}
	}

	private getDateValues(dateValues: Array<string>): Array<number> { // TODO
		if (this.datePipeOptions.includes('/')) {
			const dateFormatParts = this.datePipeOptions.toLowerCase().split('/');

			let year,
				month,
				day;

			dateFormatParts.forEach((datePart: string, i: number) => {

				if (datePart.includes('d')) {
					day = +dateValues[i];
				}

				if (datePart.includes('m')) {
					month = +dateValues[i] - 1;
				}

				if (datePart.includes('y')) {
					year = +dateValues[i];
				}
			});

			return [year, month, day];
		}

	}
}

