import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { FabricDatePickerComposition } from './models/fabric-date-picker-composition';

@Injectable()
export class FabricDatePickerCompositionService {

	private readonly datePickerFormat$ = new BehaviorSubject<FabricDatePickerComposition>(FabricDatePickerComposition.DATE_PICKER);

	onComposition(): Observable<FabricDatePickerComposition> {
		return this.datePickerFormat$.asObservable();
	}

	next(datePipeOptions: string): void {
		this.datePickerFormat$.next(this.getComposition(datePipeOptions));
	}

	private getComposition(datePipeOptions: string): FabricDatePickerComposition {
		const removeDoubles = datePipeOptions.split(':').join(' '),
			removeDash = removeDoubles.split('/').join(' '),
			removeDot = removeDash.split('.').join(' '),
			removeComa = removeDot.split(',').join(' '),
			formatArray = removeComa.split(' ');

		let composition: FabricDatePickerComposition;

		formatArray.forEach((formatItem: string) => {
			const isDays = formatItem.toLowerCase().includes('d'),
				isMonths = formatItem.includes('M'),
				isYears = formatItem.toLowerCase().includes('y'),
				showHours = formatItem.toLowerCase().includes('h'),
				showMinutes = formatItem.includes('m'),
				showSeconds = formatItem.toLowerCase().includes('s'),
				isMeridian = formatItem.includes('h'),
				showDatePicker = isDays || isMonths || isYears,
				showTimePicker = showHours || showMinutes || showSeconds;

			if (showDatePicker) {
				composition = composition | FabricDatePickerComposition.DATE_PICKER;
			}

			if (showTimePicker) {
				composition = composition | FabricDatePickerComposition.TIME_PICKER;
			}

			if (isMeridian) {
				composition = composition | FabricDatePickerComposition.TIME_PICKER_MERIDIAN;
			}

			if (showHours) {
				composition = composition | FabricDatePickerComposition.TIME_PICKER_HOURS;
			}

			if (showMinutes) {
				composition = composition | FabricDatePickerComposition.TIME_PICKER_MINUTES;
			}

			if (showSeconds) {
				composition = composition | FabricDatePickerComposition.TIME_PICKER_SECONDS;
			}

		});
		return composition;
	}
}
